@use '../base/variables' as v;

.card {
  width: 92rem;
  margin: 27rem auto;
  display: flex;
  border-radius: 1rem;
  overflow: hidden;

  &_item {
    padding: 5rem 6rem 5rem 5rem;
  }

  & .orange {
    background-color: v.$bright-orange;
  }

  & .cyan {
    background-color: v.$dark-cyan;
  }

  & .dark-cyan {
    background-color: v.$very-dark-cyan;
  }

  &_icon {
    margin-bottom: 3rem;
  }

  & .sedan {
    background: url(../images/icon-sedans.svg) no-repeat top left;
    width: auto;
    height: 4.3rem;
  }

  & .suv {
    background: url(../images/icon-suvs.svg) no-repeat top left;
    width: auto;
    height: 4.3rem;
  }

  & .luxury {
    background: url(../images/icon-luxury.svg) no-repeat top left;
    width: auto;
    height: 4.3rem;
  }

  &_h1 {
    font-size: 4rem;
    font-family: v.$display-font;
    text-transform: uppercase;
    color: v.$very-light-grey;
    margin-bottom: 2.8rem;
  }

  &_txt {
    font-size: 1.5rem;
    line-height: 1.65;
    color: v.$transpa-white;
    margin-bottom: 3rem*2.8;
  }

  &_btn {
    display: inline-block;
    background-color: v.$very-light-grey;
    padding: 1.2rem 3.1rem;
    border: v.$very-light-grey solid 2px;
    border-radius: 5rem;
    font-size: 1.5rem;
    mix-blend-mode: screen;
    cursor: pointer;

    &:hover {
      background-color: transparent;
      border: v.$very-light-grey solid 2px;
      color: v.$very-light-grey;
      mix-blend-mode: normal;
    }
  }
}

.github {
  width: max-content;
  border: v.$dark-cyan solid 1px;
  border-radius: 2rem;
  margin: 3rem auto;
  padding: 3rem;
  font-size: 1.4rem;

  & :visited, & :link {
    color: v.$dark-cyan;
  }
}

@media screen and (max-width: 50rem) {

  .card {
    flex-direction: column;
    width: clamp(33rem, 80%, 45rem);

    &_item {
      padding: 5rem;
    }

    &_txt {
      margin-bottom: 3rem;
    }
  }

  .github {
    width: clamp(33rem, 80%, 45rem);
    text-align: center;
  }
  
}